const vscode = require('vscode');
const fs = require('fs');
const path = require('path');

let reloadWatcher = null;

// Classe pour l'élément de test
class TestTreeItem extends vscode.TreeItem {
	constructor(label, description) {
		super(label);
		this.description = description;
		this.tooltip = `${label} - ${description}`;
	}
}

// Fournisseur simplifié
class TestTreeDataProvider {
	constructor() {
		this._onDidChangeTreeData = new vscode.EventEmitter();
		this.onDidChangeTreeData = this._onDidChangeTreeData.event;
	}

	refresh() {
		this._onDidChangeTreeData.fire();
	}

	getTreeItem(element) {
		return element;
	}

	getChildren() {
		// Un seul élément simplifié
		return [new TestTreeItem('Test VS Reload', 'Exemple simplifié')];
	}
}

/**
 * @param {vscode.ExtensionContext} context
 */
function activate(context) {
	console.log('Extension "VS Reload Example" activée');
	
	// === INTÉGRATION VS-RELOAD HELPER ===
	console.log('[vs-reload] Extension helper activée');
	
	// Surveiller le fichier de signal de reload
	const extensionPath = context.extensionPath;
	const signalFile = path.join(extensionPath, '.vs-reload-signal');
	
	if (fs.existsSync(extensionPath)) {
		reloadWatcher = fs.watch(extensionPath, (eventType, filename) => {
			if (filename === '.vs-reload-signal' && eventType === 'rename') {
				if (fs.existsSync(signalFile)) {
					console.log('[vs-reload] Signal de reload détecté');
					setTimeout(() => {
						vscode.commands.executeCommand('workbench.action.reloadWindow');
					}, 100);
				}
			}
		});
		
		console.log('[vs-reload] Surveillance activée');
	}
	// === FIN INTÉGRATION VS-RELOAD HELPER ===
	
	// Commande Hello World
	let disposable = vscode.commands.registerCommand('vs-reload-example.helloWorld', function () {
		vscode.window.showInformationMessage('Hello World! 🚀');
	});

	// Enregistrer vue simplifiée
	const treeDataProvider = new TestTreeDataProvider();
	vscode.window.registerTreeDataProvider('vsReloadExampleView', treeDataProvider);
	
	// Rafraîchir vue
	context.subscriptions.push(vscode.commands.registerCommand('vs-reload-example.refreshView', () => {
		treeDataProvider.refresh();
		vscode.window.showInformationMessage('Vue rafraîchie!');
	}));
	
	context.subscriptions.push(disposable);
	context.subscriptions.push({
		dispose: () => {
			if (reloadWatcher) {
				reloadWatcher.close();
				reloadWatcher = null;
			}
		}
	});
}

function deactivate() {
	if (reloadWatcher) {
		reloadWatcher.close();
		reloadWatcher = null;
	}
	console.log('[vs-reload] Extension désactivée');
}

module.exports = {
	activate,
	deactivate
}; 